/* global Sirv */

Sirv.define('Promise', {
    // eslint-disable-next-line
    load: (name, req, onload, config) => {
        if (window.Promise) {
            onload(window.Promise);
        } else {
            req(['polyfill'], function (value) {
                onload(value.Promise);
            });
        }
    }
});
