/* eslint-env es6 */
/* global sirvRequirejs */
/* global isLazyImage */
/* global SIRV_ASSETS_URL */
/* global SIRV_BASE_URL */
/* global SIRV_HTTP_PROTOCOL */
/* global sirvDeps */

/* eslint-disable indent */
/* eslint no-template-curly-in-string: "off" */
/* eslint-disable dot-notation */
/* eslint-disable no-use-before-define */
/* eslint-disable no-lonely-if */
/* eslint-disable no-case-declarations */

/* eslint-disable no-global-assign */

const w = window;

const _Sirv = {
    items: {},
    eventManager: null,
    options: {},
};

sirvRequirejs.require(['globalVariables'], (globalVariables) => {
    globalVariables.SIRV_ASSETS_URL = SIRV_ASSETS_URL;
    globalVariables.SIRV_BASE_URL = SIRV_BASE_URL;
    globalVariables.SIRV_HTTP_PROTOCOL = SIRV_HTTP_PROTOCOL;
});

const waitModules = (() => {
    const allCallbacks = [];

    const result = (callback) => {
        if (_Sirv.start) {
            callback();
        } else {
            allCallbacks.push(callback);
        }
    };

    result.start = () => {
        while (allCallbacks.length) {
            allCallbacks.shift()();
        }
    };

    return result;
})();

const baseModules = [
    'babelHelpers',
    'magicJS',
    'globalVariables',
    'globalFunctions',
    'EventManager',
    'helper',
    'Slider'
];

if (isLazyImage) { baseModules.push('LazyImage'); }

sirvRequirejs.require(baseModules, (bHelpers, magicJS, globalVariables, globalFunctions, EventManager, helper, Slider, LazyImage /* that 'LazyImage' argument must be last */) => {
    const $J = magicJS;
    // eslint-disable-next-line
    const $ = $J.$;

    const getQuerySelector = (qSelector, context) => {
        let result = qSelector;

        if ($J.typeOf(result) === 'string') {
            result = (context || document).querySelector(result);
        }

        return $J.$(result);
    };

    const findSirvItem = (qSelector, type, context) => {
        let t = [];
        let a = null;

        const storage = _Sirv.items[type];

        if (storage.length && qSelector) {
            a = getQuerySelector(qSelector, context);
            if (a) {
                t = storage.filter((_) => {
                    // return _.isEqual(a); // TODO ?????
                    return _.node === a;
                });
            }
        }

        return t.length ? t[0] : null;
    };

    const getShadowDOM = (node) => {
        let result = false;

        const parent = node.parentNode;

        if (parent) {
            if (parent instanceof ShadowRoot) {
                result = parent;
            } else {
                result = getShadowDOM(parent);
            }
        }

        return result;
    };

    const getNodes = (qSelectorAll, from) => {
        let result;
        if (!from) { from = document; }

        if ($J.typeOf(qSelectorAll) === 'collection') {
            result = qSelectorAll;
        } else if ($J.typeOf(qSelectorAll) === 'element' || $J.typeOf(qSelectorAll) === 'magicjs-element') {
            result = [qSelectorAll];
        } else {
            // shadow dom is not magicjs element
            result = $J.$A(($J.$(from).node || from).querySelectorAll(qSelectorAll || '.Sirv'));
            // result = result.concat(getSirvShadowDOM(qSelectorAll || '.Sirv'));
        }

        return result;
    };

    const getTypeOfView = (node) => {
        let result = null;
        // let tmp;

        node = $J.$(node);
        if (node.hasClass('Sirv')) {
            if (node.getTagName() === 'img' || node.attr('data-bg-src') || node.getCss('background-image') && node.getCss('background-image') !== 'none') {
                result = 'image';
            } else {
                // tmp = node.attr('data-effect');
                // if (tmp) {
                //     result = tmp;
                // } else {
                //     tmp = node.attr('data-src');
                //     if (tmp) {
                //         result = 'spin';
                //     } else {
                //         result = 'viewer';
                //     }
                // }

                // if (node.attr('data-effect') || node.attr('data-src')) {
                    result = 'viewer';
                // }
            }
        }

        return result;
    };

    const stopLaunching = (options, type) => {
        let result = false;

        let currentType = type;

        if (currentType === 'image') {
            currentType = 'lazyImage';
        }

        if (options.common[currentType] && (options.common[currentType].autostart === false || $J.contains(['false', 'off'], options.common[currentType].autostart))) {
            result = true;
        }

        if (!result && $J.browser.touchScreen && $J.browser.mobile) {
            if (options.mobile[currentType] && (options.mobile[currentType].autostart === false || $J.contains(['false', 'off'], options.mobile[currentType].autostart))) {
                result = true;
            }
        }

        return result;
    };

    _Sirv.items.image = [];
    _Sirv.items.viewer = [];

    _Sirv.start = (qSelectorAll, context, typeOfComponent, force) => {
        let effect = null;
        let type;
        let lazyInit = false;

        if ($J.typeOf(qSelectorAll) === 'boolean') {
            lazyInit = qSelectorAll;
            qSelectorAll = undefined;
        }

        $J.$A(getNodes(qSelectorAll, context)).forEach((node) => {
            const magicNode = $J.$(node);
            if (node && magicNode.hasClass('Sirv')) {
                type = getTypeOfView(magicNode.node);

                if (typeOfComponent) {
                    if (typeOfComponent !== type) {
                        type = null;
                    }
                }

                _Sirv.options.common = helper.deepExtend({}, $J.W.node['SirvOptions'] || {});
                _Sirv.options.mobile = helper.deepExtend({}, $J.W.node['SirvMobileOptions'] || {});

                if (type) {
                    if ($J.contains(['image', 'viewer'], type)) {
                        if (!force && stopLaunching(_Sirv.options, type)) { return; }
                        effect = findSirvItem(magicNode.node, type);
                        if (effect) {
                            effect.startFullInit($J.extend({}, _Sirv.options), force, lazyInit);
                        } else {
                            switch (type) {
                                case 'image':
                                    if (isLazyImage) {
                                        globalFunctions.rootDOM.attachNode(node);
                                        globalFunctions.rootDOM.resetGlobalCSS(node);

                                        effect = new LazyImage(magicNode.node, {
                                            options: {
                                                common: helper.deepExtend({}, _Sirv.options.common.lazyImage || {}),
                                                mobile: helper.deepExtend({}, _Sirv.options.mobile.lazyImage || {})
                                            }
                                        }, force);

                                        // if inline autostart option is not false
                                        if (effect.isRun) {
                                            _Sirv.items.image.push(effect);
                                            effect.setParent(_Sirv.eventManager);
                                        } else {
                                            globalFunctions.rootDOM.detachNode(node);
                                        }
                                    } else {
                                        console.info('Sirv: Image component wasn\'t found.');
                                    }
                                    break;
                                case 'viewer':
                                    globalFunctions.rootDOM.attachNode(node);
                                    globalFunctions.rootDOM.addMainStyle(node);

                                    effect = new Slider(magicNode, $J.extend({}, _Sirv.options), force, lazyInit);

                                    // if inline autostart option is not false or off
                                    if (effect.isRun) {
                                        _Sirv.items.viewer.push(effect);
                                        effect.setParent(_Sirv.eventManager);
                                    } else {
                                        globalFunctions.rootDOM.detachNode(node);
                                    }

                                    break;
                                // no default
                            }
                        }
                    }
                }
            }
        });
    };

    _Sirv.stop = (qSelectorAll, context, typeOfComponent) => {
        let types = ['image', 'viewer'];

        if (typeOfComponent) { types = [typeOfComponent]; }
        types.forEach((type) => {
            if (qSelectorAll || context) {
                $J.$A(getNodes(qSelectorAll, context)).forEach((node) => {
                    const item = findSirvItem(node, type);
                    if (item && item.stop()) {
                        _Sirv.items[type].splice(_Sirv.items[type].indexOf(item), 1);
                    }
                });
            } else {
                _Sirv.items[type] = _Sirv.items[type].filter((_item) => { return !_item.stop(true); });
            }
        });
    };

    _Sirv.getInstance = (qSelector, type, context) => {
        const inst = findSirvItem(qSelector, type, context);
        return inst ? inst.api : inst;
    };

    _Sirv.eventManager = new EventManager(_Sirv.items);

    _Sirv.addFilterCallback = (callback, first) => {
        if (first) {
            globalFunctions.viewerFilters.unshift(callback);
        } else {
            globalFunctions.viewerFilters.push(callback);
        }
    };

    _Sirv.removeFilterCallback = (callback) => {
        const newArray = [];

        globalFunctions.viewerFilters.forEach((cb) => {
            if (cb !== callback) {
                newArray.push(cb);
            }
        });

        globalFunctions.viewerFilters = newArray;
    };

    _Sirv.removeAllFilterCallback = () => {
        globalFunctions.viewerFilters = [];
    };

    globalFunctions.stop = (node, viewer) => {
        return _Sirv.stop(node, null, viewer);
    };


    /**
     * Observe DOM changes; automatically start newly added .Sirv instances and stop removed ones.
     * @return
     */
    const observeDOM = () => {
        const notChild = (node) => {
            let result = false;

            if (node.closest) {
                result = !node.closest('div.Sirv');
            } else {
                result = $J.browser.engine === 'trident';
            }

            return result;
        };

        const domChanges = {
            added: [],
            removed: []
        };

        const processDOMChanges = helper.debounce(() => {
            const added = domChanges.added.splice(0);
            const removed = domChanges.removed.splice(0);

            // Process added nodes
            added.forEach((node) => {
                node = $J.$(node);
                if (node.node && node.node.nodeType !== 3 && node.node.nodeType !== 8 && node.node.getElementsByClassName) {
                    if (node.hasClass('Sirv')) {
                        _Sirv.start(node, null, getTypeOfView(node));
                    } else if (globalFunctions.rootDOM.rootContains(node.node) && notChild(node.node)) {
                        _Sirv.start(null, node);
                    }
                }
            });

            // Process removed nodes
            const removedInstances = [];
            removed.forEach((node) => {
                node = $J.$(node);
                if (node.node && node.node.nodeType !== 3 && node.node.nodeType !== 8 && node.node.getElementsByClassName) {
                    if (node.hasClass('Sirv')) {
                        removedInstances.push(node);
                    } else {
                        [].push.apply(removedInstances, node.node.getElementsByClassName('Sirv'));
                    }
                }

                removedInstances.forEach((_node) => {
                    _node = $J.$(_node);
                    if (!$J.D.node.body.contains(_node.node)) {
                        try {
                            _Sirv.stop(_node, null, getTypeOfView(_node));
                        } catch (ex) { /* empty */ }
                    }
                });
            });
        }, 250);

        // eslint-disable-next-line
        const collectDOMChanges = (mutations, observer) => {
            for (let i = 0, l = mutations.length; i < l; i++) {
                if (mutations[i].type === 'childList') {
                    [].push.apply(domChanges.added, mutations[i].addedNodes);
                    [].push.apply(domChanges.removed, mutations[i].removedNodes);
                }
            }
            processDOMChanges();
        };

        if ($J.W.node.MutationObserver) {
            new MutationObserver(collectDOMChanges).observe($J.D.node.body, { childList: true, subtree: true, attributes: false });
        }
    };

    globalFunctions.rootDOM.resetGlobalCSS();
    globalFunctions.iconsHash.make();

    if ($J.D.node.readyState === 'loading') {
        _Sirv.start(true);
    }

    $J.D.addEvent('domready', () => {
        _Sirv.start();
        observeDOM(globalFunctions);
    });

    waitModules.start();
});

const sayDeprecated = (apiName) => {
    console.log('sirv.js: The method Sirv' + apiName + '.refresh() is deprecated. \r\n         Use Sirv' + apiName + '.stop() and Sirv' + apiName + '.start() instead.');
};

w.Sirv.version = '[[SirvJS_version]]';
w.Sirv.build = '[[build_datetime]]';
w.Sirv.options = {};
// w.Sirv.magicJS = null;

w.Sirv.lazyimage = {
    start: (qSelectorAll, context) => { waitModules(() => { _Sirv.start(qSelectorAll, context, 'image', true); }); },
    stop: (qSelectorAll, context) => { waitModules(() => { _Sirv.stop(qSelectorAll, context, 'image'); }); },
    getInstance: (qSelector, context) => {
        let result = null;

        if (_Sirv.getInstance) {
            result = _Sirv.getInstance(qSelector, 'image', context);
        }

        return result;
    },
    refresh: () => { sayDeprecated('.image'); },
    on: (nameOfEvent, callback) => w.Sirv.on(nameOfEvent, callback),
    off: (nameOfEvent, callback) => w.Sirv.off(nameOfEvent, callback)
};

w.Sirv.viewer = {
    start: (qSelectorAll, context) => { waitModules(() => { _Sirv.start(qSelectorAll, context, 'viewer', true); }); },
    stop: (qSelectorAll, context) => { waitModules(() => { _Sirv.stop(qSelectorAll, context, 'viewer'); }); },
    getInstance: (qSelector, context) => {
        let result = null;

        if (_Sirv.getInstance) {
            result = _Sirv.getInstance(qSelector, 'viewer', context);
        }

        return result;
    },
    refresh: () => { sayDeprecated('.viewer'); },
    on: (nameOfEvent, callback) => { return w.Sirv.on(nameOfEvent, callback); },
    off: (nameOfEvent, callback) => { return w.Sirv.off(nameOfEvent, callback); },
    filters: {
        add: (callback, first) => {
            if (_Sirv.addFilterCallback) {
                _Sirv.addFilterCallback(callback, first);
            } else {
                waitModules(() => {
                    _Sirv.addFilterCallback(callback, first);
                });
            }
        },

        remove: (callback) => {
            if (_Sirv.removeFilterCallback) {
                _Sirv.removeFilterCallback(callback);
            } else {
                waitModules(() => {
                    _Sirv.removeFilterCallback(callback);
                });
            }
        },

        removeAll: () => {
            if (_Sirv.removeAllFilterCallback) {
                _Sirv.removeAllFilterCallback();
            } else {
                waitModules(() => {
                    _Sirv.removeAllFilterCallback();
                });
            }
        }
    }
};

w.Sirv.start = (qSelectorAll, context) => { waitModules(() => { _Sirv.start(qSelectorAll, context, null, true); }); };
w.Sirv.stop = (qSelectorAll, context) => { waitModules(() => { _Sirv.stop(qSelectorAll, context); }); };
w.Sirv.getInstance = (qSelector, type, context) => {
    let instance = null;

    if (_Sirv.getInstance) {
        if (type === 'viewer') {
            instance = w.Sirv.viewer.getInstance(qSelector, context);
        } else if (type === 'lazyimage') {
            instance = w.Sirv.lazyimage.getInstance(qSelector, context);
        } else {
            instance = w.Sirv.viewer.getInstance(qSelector, context);
        }
    }

    return instance;
};

w.Sirv.refresh = () => { sayDeprecated(''); };

w.Sirv.on = (nameOfEvent, callback) => {
    let result = null;

    if (_Sirv.eventManager) {
        result = _Sirv.eventManager.addEvent(nameOfEvent, callback);
    } else {
        result = (() => {
            let state = 1;
            let cb = () => { return true; };
            waitModules(() => {
                if (state) {
                    cb = _Sirv.eventManager.addEvent(nameOfEvent, callback);
                }
            });

            return () => {
                state = 0;
                return cb();
            };
        })();
    }

    return result;
};

w.Sirv.off = (nameOfEvent, callback) => {
    let result = null;

    if (_Sirv.eventManager) {
        result = _Sirv.eventManager.removeEvent(nameOfEvent, callback);
    } else {
        result = (() => {
            let state = 1;
            let cb = () => { return true; };
            waitModules(() => {
                if (state) {
                    cb = _Sirv.eventManager.removeEvent(nameOfEvent, callback);
                }
            });

            return () => {
                state = 0;
                return cb();
            };
        })();
    }

    return result;
};

w.Sirv.whenReady = (callback) => {
    waitModules(callback);
};

w.Sirv.whenLoaded = (callback) => {
    sirvRequirejs.require(sirvDeps, () => {
        callback();
    });
};
