Sirv.define(
    'ComponentLoader',
    ['bHelpers','magicJS','globalVariables','globalFunctions','helper','RoundLoader'],
    (bHelpers,magicJS,globalVariables,globalFunctions,helper,RoundLoader) => {
        const moduleName = 'ComponentLoader';
        
        const $J = magicJS;
        const $ = $J.$;
        
        
        
        /* eslint-env es6 */
/* global RoundLoader */
/* global $J */
/* eslint-disable indent */
/* eslint quote-props: ["error", "as-needed", { "keywords": true, "unnecessary": false }] */


const setDefaultOptions = (options) => {
    if (!options) { options = {}; }
    if (!options.width) { options.width = '100%'; }
    if (!options.height) { options.height = '100%'; }
    return options;
};

// eslint-disable-next-line no-unused-vars
class ComponentLoader extends RoundLoader {
    constructor(parent, options) {
        options = setDefaultOptions(options);
        super(parent, options);
        this.type = 'component';

        this.loaderElement
            .addClass('smv-bounce-wrapper')
            .append($J.$new('div').addClass('smv-bounce1'))
            .append($J.$new('div').addClass('smv-bounce2'));
    }

    addClass() { this.node.addClass('smv-component-loader'); }

    destroy() {
        this.loaderElement.removeClass('smv-bounce-wrapper');
        this.loaderElement.node.innerHTML = '';
        super.destroy();
    }
}

return ComponentLoader;

    }
);
